@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap");

.wandyWrapper {
  width: 90vw;
  overflow-x: scroll;
  overflow-y: hidden;
  padding: 10vh 5vw 15vh;
  display: flex;
  align-items: center;
  font-family: Lato;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.wandyContainer {
  display: flex;
  padding-bottom: 10vh;
  align-items: center;
  /* flex-direction: column; */
}
.wandyContainer > div {
  /* transform: rotate(90deg); */
}
.wandyWrapper::-webkit-scrollbar {
  display: none;
}
.headingWandy {
  font-size: 2rem;
  line-height: 130%;
  width: 20vw;
  margin-right: 20vw;
  font-weight: 900;
}
.cardWandy {
  border-radius: 64px;
  width: 70vw;
  padding: 5vh 5vw;
  display: flex;
  justify-content: flex-start;
  margin-right: 5vw;
}
.firstCard {
  background: linear-gradient(
    90deg,
    #dff0fd 0%,
    #dff0fc 22.92%,
    #ddf4f0 61.46%,
    #dbf9de 100%
  );
}
.secondCard {
  background: linear-gradient(
    90deg,
    #fad9d9 0%,
    #fad9da 18.23%,
    #efdeef 49.48%,
    #e7e1fe 72.4%,
    #e7e1ff 100%
  );
}
.thirdCard {
  background: linear-gradient(90deg, #fff7cc 0%, #fce5d4 64.58%, #fad9d9 100%);
}

.cardContent {
  display: flex;
  flex-direction: column;
  width: 35vw;
  text-align: left;
}
.cardHeading {
  font-weight: 300;
  font-size: 50px;
  line-height: 110%;
}
.cardText {
  font-size: 20px;
  line-height: 110%;
  margin-top: 3vh;
  /* or 140% */

  display: flex;
  align-items: center;

  color: #404040;
}
.socialsContainerWandy {
  display: flex;
  justify-content: flex-start;
}
.socialsContainerWandy > img {
  aspect-ratio: 1;
  width: 30px;
  margin-right: 35px;
  margin-top: 5vh;
  cursor: pointer;
}
.cardImageWandy > img {
  margin-left: 3vw;
  height: 80vh;
  margin-bottom: -30vh;
  margin-top: -15vh;
}
.secondCard .socialsContainerWandy > img {
  aspect-ratio: unset;
}

.flipstoryWandy {
  color: #537fff;
  font-family: Montserrat;
  font-weight: 900;
}
.buttonWandy {
  color: white;
  background-color: #2d76f9;
  border-radius: 100px;
  padding: 2vh 2vw;
  cursor: pointer;
  margin-top: 5vh;
}

@media all and (max-width: 600px) {
  .wandyWrapper {
    overflow: hidden;
  }
  .wandyContainer {
    flex-direction: column;
    width: 100%;
  }
  .headingWandy {
    width: auto;
    margin-right: 0;
    margin-bottom: 5vh;
  }
  .cardWandy {
    border-radius: 30px;
    margin-right: 0;
    flex-direction: column;
    align-items: center;
    margin-bottom: 5vh;
  }
  .cardContent {
    width: 90%;
    text-align: center;
  }
  .cardHeading {
    font-size: 30px;
  }
  .cardText {
    font-size: 15px;
  }
  .socialsContainerWandy {
    justify-content: center;
  }
  .socialsContainerWandy > img {
    margin: 5vh 6vw 0;
  }
  .cardImageWandy > img {
    margin-top: 5vh;
    margin-bottom: 0;
  }
  .buttonWandy {
    padding: 2vh 6vw;
  }
}
