@import url("https://fonts.googleapis.com/css2?family=Lato:wght@500;400;700;900&display=swap");
 
.WaitlistWrapper {
  border-radius: 15px;
  background-color: #dff0fd;
  padding: 3vh 3vw 2vh;
  content: "";
  display: flex;
  flex-direction: column;
  color: black;
  align-items: flex-start;
  font-weight: 600;
  font-family: Lato;
}
.WaitlistWrapper > form {
  display: flex;
  flex-direction: column;
  color: black;
  align-items: flex-start;
}
.WaitlistWrapper input {
  border: #2d76f9 1px solid !important;
  font-family: Lato;
  border-radius: 50px;
  background-color: white;
  padding: 1vh 1vw;
  font-weight: 500;
  font-size: 0.9em;
  width: -webkit-fill-available;
}
.inputBar {
  width: 20vw;
}
.inputBar > div {
  width: 20vw;
}
.phoneNumberContainer ::before,
.phoneNumberContainer div::after {
  border: none !important;
}
.fields {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 1vh;
}
.inputLabel {
  text-align: left;
  margin-bottom: 1vh;
  font-size: 0.9em;
  font-weight: 500;
}
.inputSubLabel {
  max-width: 20vw;
  font-size: 0.9em;
  margin-top: 0.5vh;
  font-weight: 500;
  margin-bottom: 1vh;
}
.atTheRate {
  margin-right: 10px;
}
.pipeSymbol {
  opacity: 0.4;
}
.usernameInput {
  border: #2d76f9 1px solid !important;

  display: flex;
  border-radius: 50px;
  background-color: white;
}
.usernameInput > div {
  width: max-content;
  padding: 0 1vw;
  display: flex;
  line-height: 0;
  justify-content: center;
  align-items: center;
}
.usernameInput input {
  border: none !important;
  padding-left: 0;
}
.errorMessage {
  color: #ff5959;
  font-size: 0.7em;
  margin-top: 1vh;
  font-weight: 500;
  content: "something";
}

.checkboxContainer {
  font-weight: 500;
  font-size: 0.8em;
  margin-bottom: 1vh;
  display: flex;
  align-items: center;
}
.submitNewWaitlist {
  background-color: #537fff !important;
  color: white !important;
  font-family: Lato !important;
  border-radius: 50px !important;
  font-size: 1em;
  text-transform: capitalize !important;
  padding: 1vh 4vw !important;
}

@media all and (max-width: 600px) {
  .WaitlistWrapper {
    padding: 2vh 6vw 2vh;
    margin-top: 5vh;
    align-items: center;
  }
  .WaitlistWrapper form {
    align-items: center;
  }
  .inputLabel {
    font-size: 16px;
    width: 100%;
    font-weight: 500;
    max-width: 70vw;
  }
  .inputSubLabel {
    max-width: 78vw;
  }
  .inputBar {
    width: 78vw;
  }
  .inputBar > div {
    width: 78vw;
  }
  .WaitlistWrapper input {
    font-weight: 500;
    padding-left: 3vw;
  }
  .errorMessage {
    font-size: 1em;
  }
  .usernameInput > div {
    width: auto;
    padding: 0 0 0 3vw;
  }
  .checkboxContainer {
    font-size: 1em;
  }
  .submitNewWaitlist {
    font-size: 1.2em !important;
    margin-top: 2vh !important;
    padding: 1.5vh 15vw !important;
  }
}
