@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap");
@font-face {
  font-family: Lato-SemiBold;
  src: url("./../../Assets/NewLandingPage/Lato-SemiBold.ttf");
}

.countdownWrapper {
  min-height: 94vh;
  width: 90vw;
  background-color: #5eb3f7;
  color: white;
  font-family: Lato;
  font-style: normal;
  content: "";
  display: flex;
  flex-direction: column;
  padding: 3vh 5vw;
  align-items: center;
}
.countdownNav {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.whiteFlippyLogo {
  height: 2.2rem;
}
.emailWrapper {
  display: flex;
  flex-direction: column;
}
.emailContainer {
  background-color: white;
  border-radius: 56px;
  padding: 2vh 1vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-width: 20vw;
}
.greenArrowEmail {
  height: 1em;
  visibility: hidden;
  transform: translateX(-30px);
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
}
.removeIcon {
  height: 1em;
  display: none;
  cursor: pointer;
}
.emailInput {
  border: none;
  color: #404040;
  font-family: Lato;
}
.errorMessageEmail {
  text-align: left;
  margin-top: 0.5vh;
  padding-left: 2vw;
  color: black;
}
.headingCounter {
  font-weight: 900;
  font-size: 1.5em;
  line-height: 110%;
  width: 100%;
  text-align: center;
  margin-top: 2vh;
  margin-bottom: 2vh;
}
.counter {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  font-size: 5em;
  line-height: 110%;
  display: flex;
  min-width: 50vw;
  justify-content: space-evenly;
  color: white;
}
#seconds {
  color: #ea6966;
}
.counter.active {
  color: #bfe1fc;
}
.counter > div {
  display: flex;
  flex-direction: column;
}
.counterTitles {
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 0.4em;
  line-height: 110%;

  color: #ffffff;
}
.pauseButtonContainer {
  background: #7ec2f8 !important;
  box-shadow: 2.96032px 2.96032px 5.9375px #5ba4de,
    -2.96032px -2.96032px 5.9375px #77bcf2,
    -5.93903px -5.93903px 11.875px #5eb3f7, 5.93903px 5.93903px 11.875px #4e9bd8;
  border-radius: 50% !important;
  aspect-ratio: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  margin-top: 3vh !important    ;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none;
}
.pauseButtonContainerActive {
  background: #59adf0 !important;
  box-shadow: inset 2.96032px 2.96032px 5.9375px #4b9bdb,
    inset -2.96032px -2.96032px 5.9375px #85c1f0,
    inset -5.93903px -5.93903px 11.875px #6cb2ea,
    inset 5.93903px 5.93903px 11.875px #5eb3f7 !important;
}
.pauseButtonLines {
  height: 30px;
  margin: 0 3px;
  width: 8px;
  content: "";
  background-color: white;
  border-radius: 5px;
}
.toolTipText {
  font-size: 1.2em !important;
  text-align: center;
  font-family: Lato-SemiBold !important;
}
.countdownContentContainer {
  width: 100%;
  margin-top: -5vh;
  display: flex;
  justify-content: space-between;
}
.countdownContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 95%;
  width: 20vw;
  text-align: left;
}
.countdownContentHeading {
  font-weight: 800;
  font-size: 4rem;
  line-height: 120%;
  white-space: pre-line;
  margin-bottom: 5vh;
  width: 1vw;
}

.scrollToDiscover {
  position: absolute;
  bottom: 2vh;
  display: flex;
  font-size: 18px;
  align-items: center;
}
.downArrow {
  height: 12px;
  margin: 0 10px;
}

@media all and (max-width: 600px) {
  .countdownWrapper {
    position: relative;
    padding-bottom: 6vh;
  }
  .emailWrapper {
    position: absolute;
    bottom: 2vh;
  }
  .countdownNav {
    flex-direction: column;
    align-items: center;
  }
  .whiteFlippyLogo {
    width: 30vw;
    height: auto;
  }
  .emailContainer {
    padding: 1vh 4vw;
    border-width: 2px !important;
    margin-top: 1vh;
    font-size: 16px;
  }
  .counter {
    font-size: 4em;
    min-width: 80vw;
  }
  .greenArrowEmail {
    height: 0.8em;
  }
  .removeIcon {
    height: 0.8em;
  }
  .headingCounter {
    margin-top: 2vh;
    margin-bottom: 2vh;
  }
  .pauseButtonContainer {
    width: 25px;
  }
  .pauseButtonLines {
    height: 25px;
  }
  .MuiTooltip-tooltip {
    font-size: 1.2em !important;
    padding: 2vh 3vw !important;
  }
  .toolTipText {
    font-size: 1.1em !important;
    font-weight: 550 !important;
  }
  .countdownContentContainer {
    flex-direction: column;
    width: 100%;
    margin-top: 0;
    margin-bottom: 6vh;
  }
  .countdownContent {
    width: 100%;
    text-align: center;
  }
  .countdownContentHeading {
    width: 100%;
    margin-bottom: 1vh;
    margin-top: 1.5vh;
  }
  .scrollToDiscover {
    position: initial;
    font-size: 16px;
    margin-top: 3vh;
    display: none;
  }
  .downArrow {
    height: 10px;
    margin: 0 10px;
  }
}
