@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap");

body {
  overflow-x: hidden;
  font-family: Lato;
}
.mainHeading {
  font-size: 2em;
  font-weight: 900;
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
}
.heroSection {
  background: #7ec2f8;
  height: 95vh;
  width: 80vw;
  display: flex;
  flex-direction: column;
  padding: 5vh 10vw 0 10vw;
  align-items: flex-start;
}
.wrapper {
  width: 100vw;
  display: flex;
  flex-direction: column;
}
.subTitle {
  font-weight: 900;
  font-size: 15px;
  line-height: 92%;
  margin-top: 10vh;
  text-transform: uppercase;
  color: #ffffff;
}
.mainTitle {
  font-weight: 900;
  font-size: 50px;
  text-transform: uppercase;
  color: #ffffff;
}
.heroContentAndVideo {
  width: 100%;
  height: 35vh;
  display: flex;
  justify-content: space-between;
  margin-top: 5vh;
}
.heroVideo {
  height: 100%;
  width: 40%;
}
.heroContentAndEmail {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 40%;
  align-items: flex-start;
}
.heroContent {
  width: 25vw;
  font-weight: 700;
  font-size: 20px;
  border-left: 7px solid #fff7cc;
  text-align: left;
  padding-left: 2vw;
  line-height: 140%;
  margin-bottom: 15vh;
}
input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
input:focus {
  outline: none;
}
.inputEmail,
.inputEmailFirst {
  padding: 1em 1em;
  color: #6e6e6e;
  font-size: 1em;
  line-height: 135%;
  font-family: Lato;
  font-weight: 400;
  background: white;
  border: 1px solid transparent;
  box-sizing: border-box;
  border-radius: 4px 0 0 4px;
  width: 20vw;
}
.inputEmailButton,
.inputEmailButtonFirst {
  background-color: #537fff;
  border: 1px solid #537fff;
  box-sizing: border-box;
  border-radius: 0 4px 4px 0;
  padding: 1em 1em;
  color: #ffffff;
  font-weight: 700;
  line-height: 135%;
  font-family: Lato;
  font-size: 1em;
  cursor: pointer;
}

.cloudImage {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100vw;
}
.pageContent {
  width: 80vw;
  padding: 10vh 10vw;
  height: 70vh;
}
.secondSectionTitle {
  font-style: normal;
  font-weight: 900;
  font-size: 40px;
  text-align: left;
  line-height: 92%;
  color: #b6b6b6;
  /* margin-top: 20vh; */
}
.secondSectionContentAndIllustration {
  margin-top: 10vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}
.flipstoryFont {
  font-weight: 900;
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  color: #5881e0;
  margin: 0 5px;
}
.secondSectionContentContainer {
  /* width: 40vw; */

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.secondSectionContent {
  font-size: 20px;
  width: 25vw;
  margin-top: 5vh;
  font-weight: 700;
  text-align: left;
}
.secondSectionIllustration {
  max-width: 40vw;
  max-height: 50vh;
}
.secondSectionEmailContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.secondSectionEmail {
  display: flex;
  margin-top: -4em;
}
.secondSectionEmail > .inputEmail,
.secondSectionEmail > .inputEmailButton {
  border-color: #d8d8d8;
  font-size: 1em;
  padding: 1em;
}
