.footer {
  background: #303030;
  width: 90vw;
  padding: 5vh 5vw;
  display: flex;
  justify-content: space-between;
}
.footerSocialsAndCopyright {
  width: 30vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footerSocials {
  margin-bottom: 10vh;
  align-items: flex-start;
  display: flex;
  flex-direction: column;
}
.footerFlippy {
  width: 10vw;
  margin-bottom: 2vh;
  cursor: pointer;
}
.footerSocialContainer img {
  width: 25px;
  margin-right: 25px;
  cursor: pointer;
}
.footerCopyright {
  color: white;
  text-align: left;
  font-weight: 400;
  font-size: 12px;
}
.footerCompanyLinksContainer {
  display: flex;
  flex-grow: 1;
  justify-content: space-around;
}
.footerCompanyLinks {
  align-self: flex-end;
  display: flex;
  flex-direction: column;
}
.footerCompanyLinks a {
  font-size: 15px;
  line-height: 200%;
  font-weight: 400;
  color: #737373;
  text-align: left;
  cursor: pointer;
}
.footerCompanyLinks a:nth-child(1) {
  font-weight: 900;
  cursor: default;
}
@media all and (max-width: 800px) {
  .heroVideo {
    height: 30vh;
    width: 50%;
  }
  .inputEmail,
  .inputEmailButton {
    padding: 0.7em;
    font-size: 0.7em;
  }
  .secondSectionTitle {
    margin-top: 10vh;
  }
  .secondSectionContentAndIllustration {
    margin-top: 10vh;
  }
}
@media all and (max-width: 500px) {
  .mainHeading {
    font-size: 3em;
    width: 100%;
    text-align: center;
  }
  .heroSection {
    align-items: center;
    height: max-content;
    min-height: 98vh;
    padding-top: 2vh;
    justify-content: flex-end;
  }
  .subTitle {
    margin-top: 5vh;
  }
  .mainTitle {
    font-weight: 600;
    font-size: 30px;
    width: 50%;
    line-height: 130%;
    text-align: center;
  }
  .heroContentAndVideo {
    flex-direction: column;
    align-items: center;
    height: max-content;
  }
  .heroContentAndEmail {
    width: 100%;
  }
  .heroContent {
    width: 100%;
    font-size: 15px;
    border-bottom: 4px solid #fff7cc;
    font-weight: 600;
    border-left: none;
    text-align: center;
    padding: 0 0 3vh 0;
    margin-bottom: 6vh;
  }
  .heroEmail {
    width: 100%;
  }
  .inputEmail {
    width: 50vw;
    font-size: 1.5em;
  }
  .inputEmailButton {
    font-size: 1.5em;
  }
  .heroVideo {
    height: 20vh;

    width: 90%;
    margin-top: 4vh;
  }
  .cloudImage {
    justify-self: flex-end;
    position: relative;
  }
  .pageContent {
    width: 90vw;
    min-height: 70vh;
    height: auto;
    padding: 10vh 5vw;
    align-items: center;
    display: flex;
    flex-direction: column;
  }
  .secondSectionTitle {
    margin-top: 0;
    text-align: center;
    font-size: 25px;
    line-height: 120%;
    font-weight: 600;
  }
  .secondSectionContentAndIllustration {
    margin-top: 5vh;
    flex-direction: column;
    align-items: center;
  }
  .secondSectionContent {
    font-size: 17px;
    width: 85%;
    text-align: left;
    margin-top: 0;
  }
  .secondSectionIllustration {
    max-width: 100%;
    margin-top: 5vh;
  }
  .secondSectionEmail {
    margin-top: 8vh;
  }
  .secondSectionEmail > .inputEmail,
  .secondSectionEmail > .inputEmailButton {
    font-size: 1.5em;
    padding: 0.7em;
  }
  .thirdSectionIllustration {
    width: 100%;
    margin-top: 5vh;
  }
  .thirdSectionContent {
    font-size: 17px;
    line-height: 120%;
    text-align: center;
    margin-top: 5vh;
  }
  .footer {
    flex-direction: column-reverse;
    padding: 5vh 10vw;
    width: 80vw;
  }
  .footerCompanyLinksContainer {
    justify-content: space-between;
    border-bottom: 1px solid #7d7d7d;
    padding-bottom: 3vh;
  }
  .footerCompanyLinks a {
    font-size: 12px;
    text-align: center;
  }
  .footerSocialsAndCopyright {
    width: 100%;
    margin-top: 3vh;
  }
  .footerFlippy {
    width: 25vw;
  }
  .footerSocialContainer img {
    width: 18px;
    margin-right: 18px;
  }
  .footerSocials {
    margin-bottom: 2vh;
  }
  .footerCopyright {
    font-size: 9px;
  }
}
