.joinCommunityContainer {
  padding: 10vh 10vw;
  background: #537fff;
  width: 80vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.joinCommunityHeading {
  font-weight: 700;
  font-size: 3em;
  color: white;
}
.joinCommunitySubHeading {
  color: #ffffff;
  font-size: 1.5em;
  font-weight: 500;
  opacity: 0.5;
  margin-top: 3vh;
  margin-bottom: 5vh;
}

.socialsContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 5vh;
}
.socials {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.socialsBackground {
  filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.12));
  background: #ffffff30;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 80px;
  width: 80px;
}
.socialsBackground > img {
  height: 50px;
}
.socialHandles {
  font-weight: 400;
  font-size: 20px;
  line-height: 110%;
  color: #ffffff;
  margin-top: 2vh;
  cursor: pointer;
}

.footerSE {
  padding: 10vh 10vw 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.footerLogo {
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  width: 100%;
  padding-bottom: 5px;
  margin-bottom: 3vh;
}
.footerLogo > img {
  width: 15vw;
}
.copyright {
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 110%;
  /* identical to box height */

  color: rgba(0, 0, 0, 0.5);
  margin-bottom: 4vh;
}
