@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gloria+Hallelujah&display=swap");

.horizontalWrapper {
  width: 90vw;
  height: 90vh;
  margin-bottom: 20vh;
  padding: 5vh 5vw;
  background-color: #7ec2f8;
  display: flex;
  justify-content: space-evenly;
  font-family: "Lato";
}
.leftWrapper {
  height: 90vh;
  /* overflow: scroll; */
  scroll-behavior: smooth;
  width: 40vw;
  display: flex;
  flex-direction: column;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.leftWrapper::-webkit-scrollbar {
  display: none;
}

.leftContentWrapper {
  height: 90vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  color: white;
  margin-bottom: 40vh;
}
.leftContentWrapper:last-child {
  margin-bottom: 15vh;
}
.contentWrapper {
  display: flex;
  margin-bottom: 3vh;
  flex-direction: row;
  align-items: flex-end;
}
.number {
  font-weight: 800;
  font-size: 130.42px;
  /* line-height: 160%; */
}
.titleHorizontal {
  font-weight: 500;
  font-size: 20px;
  text-align: left;
  margin-left: 1vw;
  margin-bottom: 3vh;
}

.testimonialWrapper {
  width: 30vw;
  font-weight: 400;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  padding: 4vw 3vw;
  justify-content: space-between;
  color: black;
}
.testimonialName {
  font-family: "Gloria Hallelujah", cursive;
  font-size: 30px;
}
.testimonialTitle {
  text-align: left;
  font-size: 18px;
  margin-bottom: 5vh;
}
.testimonialImageAndName {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.testimonialImage {
  display: flex;
  justify-content: center;
  align-items: center;
}
.testimonialImage > img {
  aspect-ratio: 1;
  width: 80px;
}
.testimonialNameAndPost {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-left: 2vw;
}
.testimonialPost {
  font-size: 15px;
}
.firstWrapper {
  background: linear-gradient(90deg, #fff7cc 0%, #fad9d9 100%);
}
.thirdWrapper {
  background: linear-gradient(
    90deg,
    #dff0fd 0%,
    #dff0fc 22.92%,
    #ddf4f0 61.46%,
    #dbf9de 100%
  );
}
.fourthWrapper {
  background: linear-gradient(
    90deg,
    #fad9d9 0%,
    #fad9da 18.23%,
    #efdeef 49.48%,
    #e7e1fe 72.4%,
    #e7e1ff 100%
  );
}
.rightWrapper {
  height: 90vh;
  width: 40vw;
  /* overflow: scroll; */
  scroll-behavior: smooth;
  display: flex;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.rightWrapper::-webkit-scrollbar {
  display: none;
}
.horizontalImage {
  width: 40vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.horizontalImage > img {
  width: 30vw;
  /* margin-right: 10vw; */
}

@media all and (max-width: 600px) {
  .horizontalWrapper {
    flex-direction: column;
    height: auto;
    width: 80vw;
    padding: 5vh 10vw;
  }
  .leftWrapper {
    width: 100%;
    height: auto;
  }
  .leftContentWrapper {
    align-items: center;
    height: auto;
  }
  .contentWrapper {
    flex-direction: row;
    align-items: center;
    width: 100%;
  }
  .titleHorizontal {
    text-align: left;
    margin-bottom: 0;
    margin-left: 20px;
  }
  .number {
    font-size: 100px;
  }
  .testimonialWrapper {
    width: 70vw;
    padding: 3vh 5vw;
  }
  .testimonialName {
    font-size: 23px;
  }
  .rightWrapper {
    height: auto;
    width: 100%;
    align-items: center;
    justify-content: center;
  }
  .horizontalImage > img {
    width: 70vw;
  }
  .horizontalImage {
    width: auto;
  }
  .leftContentWrapper:last-child {
    margin-bottom: 5vh;
  }
}
