@import url("https://fonts.googleapis.com/css2?family=Lato:wght@200;300;400;700&display=swap");

.wrapperSuccessEmailer {
  width: 100vw;
  display: flex;
  flex-direction: column;
  font-family: Lato, sans-serif;
}
.heroSectionSE {
  background-color: #fad9d9;
  width: 100vw;
  height: 95vh;
  display: flex;
  padding-top: 5vh;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}
.heroSectionSE > div {
  z-index: 1;
}
.logo {
  width: 10vw;
}
.heroSectionWelcome {
  margin-top: 10vh;

  font-weight: 300;
  font-size: 4em;
}
.heroSectionUsername {
  font-family: Lato;
  font-weight: 700;
  font-size: 4em;
}
.heroSectionText {
  margin-top: 5vh;

  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 110%;
  text-align: center;
  width: 50vw;
  color: rgba(0, 0, 0, 0.5);
}
.heroSectionIllustrations {
  position: absolute;
  bottom: 0;
  right: 0;
  height: 100vh;
  z-index: 0;
}
.heroSectionIllustrationsAfter {
  position: absolute;
  bottom: -10vh;
  left: 0;
  z-index: 0;
}
.pageContentSE {
  width: 90vw;
  padding: 10vh 5vw;
  height: max-content;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nonBoldTitle {
  font-weight: 200;
  font-size: 4em;
}
.boldTitle {
  font-weight: 700;
  font-size: 4em;
  margin-bottom: 15vh;
}

.pageContentImageAndTextContainer {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 5vh;
}
div.pageContentImageAndTextContainer:nth-of-type(2n) {
  flex-direction: row-reverse;
}
.pageContentImage {
  width: 30vw;
}
.pageContentText {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  width: 30vw;
  line-height: 120%;
  color: rgba(0, 0, 0, 0.5);
}
.pageContentTextHeading {
  font-weight: 700;
  font-size: 24px;
  line-height: 110%;
  margin-bottom: 2vh;
  color: #537fff;
}
.timerHeading {
  font-weight: 200;
  font-size: 3em;
  margin-top: 20vh;
  margin-bottom: 10vh;
}
.timerHeadingBold {
  font-weight: 700;
}
.timerCountContainer {
  display: flex;
  flex-direction: column;
  color: #404040;
  font-weight: 400;
  font-size: 20px;
  line-height: 110%;
  text-transform: uppercase;
}
.timer {
  display: flex;
  flex-direction: row;
  width: 50vw;
  justify-content: space-evenly;
}
.timerCount {
  background: #b7f3bd;
  border-radius: 10px;
  color: black;
  font-weight: 900;
  padding: 30px;
  aspect-ratio: 1;

  font-size: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2vh;
}

.referralContainer {
  width: 40vw;
  margin-top: 20vh;
  font-size: 15px;
  line-height: 110%;
  display: inline-block;
  position: relative;
  color: rgba(0, 0, 0, 0.5);
  justify-content: center;
}
.referralBackground {
  width: 100%;
  height: auto;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.referralContent {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.referralCode {
  font-weight: 700;
  font-size: 17px;
  line-height: 110%;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #707070;
}
.referralBox {
  background: white;
  width: 15vw;
  border-radius: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 10vh;
  padding: 1.5vh 2vw;
  margin-bottom: 3vh;
}
.copyReferralIcon {
  height: 30px;
  cursor: pointer;
}

.buttonSE {
  background: #537fff;
  padding: 3vh 4vw;
  border-radius: 100px;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  color: white;
  cursor: pointer;
  font-size: 25px;
  margin-bottom: 10vh;
}
