@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");

.htcswrapper {
  width: 90vw;
  background: #7ec2f8;
  display: flex;
  flex-direction: column;
  color: white;
  font-size: 2.5rem;
  justify-content: center;
  align-items: center;
  font-family: Lato;
  padding: 0 5vw;
  font-weight: 900;
}

.heading1 {
  font-size: 3rem;
  line-height: 170%;
}
.sunglassesWrapper {
  font-size: 4rem;
  display: flex;
  align-items: center;
  margin-bottom: 20vh;
}
.sunglasses {
  width: 80px;
  margin: 0 5px;
}

.lastHeading {
  line-height: 200%;
  margin-top: 20vh;
  margin-bottom: 20vh;
}
.curvedBottom {
  width: 100vw;
  margin-top: 10vh;
  transform: rotate(180deg);
}
.curvedTop {
  width: 100vw;
  margin-bottom: 10vh;
}

@media all and (max-width: 600px) {
  .lastHeading {
    line-height: 110%;
  }
}
