@media screen and (min-width: 901px) {
  #cool-stuff-carousel .show-on-mobile {
    display: none;
  }
}

@media screen and (max-width: 900px) {
  #cool-stuff-carousel .hide-on-mobile {
    display: none;
  }

  #cool-stuff-carousel .show-on-mobile {
    margin: 2.667vw 0 0 4vw;
    padding: 4.8vw 8.8vw 4.8vw 4.533vw;
    width: 81.333vw;
  }

  #cool-stuff-carousel .carousel .control-dots {
    margin-left: 22.5% !important;
    margin-top: 0 !important;
    margin-bottom: 6vw !important;
  }

  #cool-stuff-carousel .carousel-root {
    height: 184.667vw;
  }

  #cool-stuff-carousel .carousel-slider {
    height: 184.667vw;
  }

  #cool-stuff-carousel .carousel .slide {
    position: static;
  }
}
