@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");

.wrapperGetStarted {
  width: 90vw;
  padding: 5vh 5vw;
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  margin-bottom: 10vh;
}
.headingGetStarted {
  font-family: Lato;
  font-size: 3rem;
  font-weight: 300;
}

.getStartedImage {
  height: calc(90vh - 10rem);
}
.waitlistHeading {
  font-size: 3rem;
  margin-bottom: 10vh;
  margin-top: 10vh;
  font-weight: 800;
}
.secondWaitlistContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 25vh;
}
.secondWaitlistContainer > div {
  width: max-content;
}
@media all and (max-width: 600px) {
  .getStartedImage {
    width: 80vw;
  }
}
