@import url("https://fonts.googleapis.com/css2?family=Lato:wght@500;400;700;900&display=swap");

.welcomeCarouselWrapper {
  padding: 10vh 5vw 20vh;
  width: 90vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-family: Lato;
}
.welcomeCarouselContainer {
  display: flex;
  padding: 10vh 10vw;
  width: 80vw;
  border-radius: 40px;
  background-size: cover;
  justify-content: space-evenly;
}
.welcomeContent {
  width: 30vw;
  font-weight: 300;
  font-size: 3em;
}
.welcomeImagesContainer {
  display: flex;
  height: 20vh;
  overflow-y: visible;
}
.welcomeImagesContainer img {
  height: 60vh;
}
.welcomeImage1 {
  margin-top: -15vh;
}
.welcomeImage2 {
  margin-top: -10vh;
}
@media all and (max-width: 600px) {
  .welcomeCarouselContainer {
    display: flex;
    padding: 10vh 5vw;
    width: 80vw;
    flex-direction: column;
    background-size: 90vw;
    background-repeat: no-repeat;
  }
  .welcomeCarouselWrapper {
    padding-bottom: 10vh;
  }
  .welcomeContent {
    width: auto;
  }
  .welcomeImagesContainer {
    height: auto;
    margin-top: 5vh;
  }
  .welcomeImagesContainer > img {
    height: auto;
    margin-top: 0;
    width: 40vw;
  }
}
