@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");

.backedByWrapper {
  width: 90vw;
  padding: 15vh 5vw;
  display: flex;
  flex-direction: column;
  font-family: Lato;
  align-items: center;
}
.backedByHeading {
  font-size: 2.5rem;
  font-weight: 900;
}
.backedByLogoContainer {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-evenly;
  margin: 5vh 0;
}
.backedByImagesContainer {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}
.backedByLogoContainer > img {
  filter: grayscale(100%);
  transition: 0.4s ease;
}
.backedByLogoContainer > img:nth-of-type(2) {
  opacity: 0.5;
  width: 30vw;
}
.imageAndTextContainer {
  width: 15vw;
}
.imageAndTextContainer > img {
  width: 10vw;
  border-radius: 15px;
  filter: grayscale(100%);
  cursor: pointer;
  transition: 0.4s ease;
}
.imageAndTextContainer > div {
  visibility: hidden;
  font-size: 32px;
  font-weight: 600;
  margin-top: 5vh;
}
.imageAndTextContainer > img:hover {
  filter: grayscale(0%);
  transform: scale(1.2);
}
.backedByLogoContainer > img:hover {
  filter: grayscale(0%);
  opacity: 1;
}
.imageAndTextContainer > img:hover + .textContainer {
  visibility: visible;
}

.FAQWrapper {
  width: 80vw;
  padding: 10vh 10vw;
  display: inline-block;
  position: relative;
}
.FAQContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70vw;
  border-radius: 80px;
  padding: 10vh 5vw;
  background-color: #fff7cc;
}
.FAQHeading {
  font-weight: 700;
  font-size: 3rem;
  margin-bottom: 5vh;
}
.FAQIllustration1 {
  position: absolute;
  top: 0;
  left: 15vw;
}
.FAQIllustration2 {
  position: absolute;
  transform: scale(0.5);
  top: -5vw;
  right: 10vw;
}
.FAQIllustration3 {
  position: absolute;
  bottom: 0;
  left: 25vw;
  transform: scale(0.8);
}
.footerNewLandingPageContainer {
  background-color: #303030;
}
.curvedTopFooter {
  width: 100vw;
}
.textContainer > span {
  display: none;
}
@media all and (max-width: 600px) {
  .backedByLogoContainer {
    flex-direction: column;
    margin-top: 5vh;
  }
  .backedByLogoContainer > img {
    width: 40vw;
  }
  .backedByLogoContainer > img:last-child {
    width: 60vw;
    margin: 5vh 0;
  }
  .backedByImagesContainer {
    flex-direction: column;
    align-items: center;
  }
  .imageAndTextContainer {
    display: flex;
    justify-content: flex-start;
    width: 90%;
    margin-bottom: 5vh;
  }
  .imageAndTextContainer > div {
    visibility: visible;
    margin-top: 0;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .textContainer {
    margin-top: 0;
    visibility: visible;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  .backedByLogoContainer > img {
    opacity: 1;
    filter: grayscale(0);
  }
  .imageAndTextContainer > img:hover {
    filter: grayscale(0%);
    transform: scale(1);
  }
  .imageAndTextContainer > img {
    filter: grayscale(0%);
    margin-right: 5vw;
    width: 30vw;
  }
  .textContainer > span {
    display: flex;
    font-weight: 400;
    font-size: 13px;
    color: #404040;
    margin-top: 1vh;
  }
  .FAQWrapper {
    width: 90vw;
    padding: 10vh 5vw;
  }
  .FAQContainer {
    width: 80vw;
    border-radius: 40px;
  }
  .FAQHeading {
    font-size: 2rem;
    z-index: 1;
  }
  .FAQContainer > div > div {
    width: 75vw !important;
  }
  .FAQIllustration1 {
    transform: scale(0.5);
  }
  .FAQIllustration2 {
    position: absolute;
    transform: scale(0.3);
    top: -15vw;
    right: 5vw;
  }
  .FAQIllustration3 {
    position: absolute;
    bottom: 0;
    left: 10vw;
    transform: scale(0.5);
  }
}
